.phone-icon {
  display: inline-block;
  width: 30px;
  height: 30px;
  background-image: url(../img/icons/call.svg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

.arrow-down {
  display: inline-block;
  width: 15px;
  height: 8px;
  background-image: url(../img/icons/arrow-down.svg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

@media (max-width: 480.98px) {
  .arrow-down {
    width: 10px;
    height: 6px;
    margin-top: 0;
  }
}

.arrow-right-icon {
  display: inline-block;
  height: 12px;
  width: 25px;
  background-image: url(../img/icons/arrow-right.svg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

@media (max-width: 767.98px) {
  .arrow-right-icon {
    width: 16px;
    height: 8px;
  }
}

.burger-icon {
  display: inline-block;
  width: 21px;
  height: 21px;
  background-image: url(../img/icons/burger-icon.svg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

* {
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Gilroy', sans-serif;
}

button {
  cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
a {
  margin: 0;
  padding: 0;
  line-height: 1;
}

a {
  color: inherit;
  text-decoration: none;
  transition: all 0.3s;
}

a:hover {
  color: inherit;
  text-decoration: none;
}

ul,
ol {
  list-style-type: none;
}

img {
  max-width: 100%;
}

.pt-42 {
  padding-top: 42px;
}

@media (max-width: 991.98px) {
  .pt-42 {
    padding-top: 19px;
  }
}

.me-5 {
  margin-right: 5px;
}

.me-49 {
  margin-right: 49px;
}

@media (max-width: 1199.98px) {
  .me-49 {
    margin-right: 15px;
  }
}

.me-57 {
  margin-right: 57px;
}

@media (max-width: 1199.98px) {
  .me-57 {
    margin-right: 35px;
  }
}

@media (max-width: 767.98px) {
  .me-57 {
    margin-right: 0px;
  }
}

.mt-5 {
  margin-top: 5px;
}

.ms-28 {
  margin-left: 28px;
}

.anim-arrow-right {
  position: relative;
}

.anim-arrow-right.price-btn {
  padding-right: 103px;
}

.anim-arrow-right:hover .arrow-right-icon {
  right: 40px;
}

@media (max-width: 767.98px) {
  .anim-arrow-right:hover .arrow-right-icon {
    right: 17px;
  }
}

.anim-arrow-right .arrow-right-icon {
  position: absolute;
  top: 50%;
  right: 60px;
  transform: translateY(-50%);
  transition: 0.4s all;
}

@media (max-width: 767.98px) {
  .anim-arrow-right .arrow-right-icon {
    right: 27px;
  }
}

.d-flex {
  display: flex;
  flex-wrap: wrap;
}

.align-items-center {
  align-items: center;
}

.align-items-start {
  align-items: flex-start;
}

@media (max-width: 767.98px) {
  .align-items-start {
    justify-content: center;
  }
}

.gradient-text {
  background: linear-gradient(106.59deg, #6F72D9 2.24%, #2AD1AB 89.64%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

@media (max-width: 991.98px) {
  .hidden-to-lg {
    display: none;
  }
}

@media (min-width: 992px) {
  .hidden-from-lg {
    display: none;
  }
}

.header-nav {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-between;
}

.header-menu {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-between;
}

@media (max-width: 991.98px) {
  .header-menu {
    display: none;
  }
}

.header-menu li {
  margin-right: 56px;
}

@media (max-width: 1199.98px) {
  .header-menu li {
    margin-right: 15px;
  }
}

.header-menu li:last-child {
  margin-right: 0;
}

.header-menu li a {
  font-weight: 500;
  font-size: 18px;
  line-height: 83%;
  color: #18243E;
}

.phone-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}

@media (max-width: 991.98px) {
  .phone-wrapper {
    display: none;
  }
}

.phone-wrapper .phone-num {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.phone-wrapper .phone-num__link {
  font-weight: bold;
  font-size: 19px;
  line-height: 88%;
  color: #18243E;
  margin-bottom: 5px;
}

.phone-wrapper .phone-num__get {
  font-size: 16px;
  line-height: 94%;
  color: #000000;
  border-bottom: #2AD1AB solid 2px;
  padding-bottom: 4px;
}

@media (max-width: 991.98px) {
  .lang-dropdown {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
}

.lang-dropdown a {
  font-weight: bold;
  font-size: 20px;
  line-height: 97%;
  color: #18243E;
  text-shadow: 0px 1px 5px rgba(28, 42, 92, 0.2);
  text-transform: uppercase;
}

@media (max-width: 480.98px) {
  .lang-dropdown a {
    font-size: 14px;
  }
}

.lang-dropdown-list {
  position: absolute;
  right: 0;
  opacity: 0;
  visibility: hidden;
  transition: 0.4s;
}

@media (max-width: 991.98px) {
  .lang-dropdown-list {
    right: auto;
    top: 20px;
    left: 20px;
  }
}

@media (max-width: 480.98px) {
  .lang-dropdown-list {
    left: 15px;
  }
}

.lang-dropdown-list li {
  margin-top: 10px;
}

.lang-dropdown__current {
  position: relative;
}

.lang-dropdown__current:hover .lang-dropdown-list {
  opacity: 1;
  visibility: visible;
}

.burger__btn {
  display: none;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  border-width: 0;
  cursor: pointer;
}

@media (max-width: 991.98px) {
  .burger__btn {
    display: flex;
  }
}

@media (max-width: 480.98px) {
  .logo-wrapper {
    max-width: 66px;
  }
}

h1 {
  font-weight: bold;
  font-size: 45px;
  line-height: 122%;
  color: #18243E;
}

@media (max-width: 767.98px) {
  h1 {
    font-size: 23px;
  }
}

.header {
  overflow-x: hidden;
}

.header::before {
  position: absolute;
  top: 0;
  left: -80px;
  content: 'U';
  font-weight: 900;
  font-size: 660px;
  line-height: 114%;
  color: #2AD1AB;
  opacity: 0.1;
  z-index: -1;
}

@media (max-width: 767.98px) {
  .header {
    text-align: center;
  }
}

.header__container {
  margin-top: 93px;
}

@media (max-width: 767.98px) {
  .header__container {
    margin-top: 21px;
  }
}

.header h1 {
  margin-bottom: 18px;
}

.header p {
  font-weight: 600;
  font-size: 30px;
  line-height: 133%;
  color: #18243E;
  margin-bottom: 65px;
}

@media (max-width: 991.98px) {
  .header p {
    margin-bottom: 34px;
  }
}

@media (max-width: 767.98px) {
  .header p {
    font-size: 16px;
  }
}

@media (max-width: 767.98px) {
  .header .d-flex {
    flex-direction: column;
    align-items: center;
  }
}

.header .get-price {
  max-width: 291px;
  font-weight: 600;
  font-size: 22px;
  line-height: 127%;
  color: #18243E;
}

@media (max-width: 767.98px) {
  .header .get-price {
    font-size: 16px;
  }
}

@media (max-width: 767.98px) {
  .header .price-btn {
    margin-bottom: 24px;
    padding-right: 57px;
  }
}

@media (max-width: 991.98px) {
  .header-img {
    text-align: center;
    margin-bottom: 55px;
  }
  .header-img img {
    max-width: 209px;
  }
}

.price-btn {
  padding: 14px 57px;
  background-color: #6F72D9;
  box-shadow: 0px 0px 25px 4px rgba(111, 114, 217, 0.4);
  border-width: 0;
  border-radius: 37px;
  font-weight: bold;
  font-size: 21px;
  line-height: 160%;
  text-align: center;
  color: #FFFFFF;
}

@media (max-width: 767.98px) {
  .price-btn {
    font-size: 13px;
  }
}
